import { Component, Input } from '@angular/core';
export interface AvatarItem {
  imgPath?: string;
  name: string;
}

@Component({
  selector: 'custom-avatars',
  imports: [],
  templateUrl: './custom-avatars.component.html',
  styleUrl: './custom-avatars.component.css'
})
export class CustomAvatarsComponent {
  @Input() items: AvatarItem[] = [];
  @Input() size = 40;  // size in px
 @Input() overlapOffset = 15;
  getInitials(name: string): string {
    return name
      .split(' ')
      .map(part => part.charAt(0).toUpperCase())
      .join('');
  }
  getRandomColor(): string {
  const colors = ['#e57373', '#64b5f6', '#81c784', '#ffd54f', '#ba68c8'];
  return colors[Math.floor(Math.random() * colors.length)];
}
}
